import { GatsbySeo, JsonLd } from "gatsby-plugin-next-seo";
import React, { FC } from "react";
import { Helmet } from "react-helmet";
import DesktopLogo from "../../../images/Logo/Telgorithm/DesktopLogo.svg";
import { isBrowser } from "../../../utils/commonUtils";

export const TopLevelPageJSONLDSchemas = () => {
  return (
    <>
      <JsonLd
        json={{
          "@context": "https://schema.org",
          "@type": "WebSite",
          name: "Telgorithm",
          url: isBrowser ? document?.URL : "",
        }}
      />
      <JsonLd
        json={{
          "@context": "https://schema.org",
          "@type": "Corporation",
          name: "Telgorithm",
          url: isBrowser ? document?.URL : "",
          logo: DesktopLogo,
          sameAs: [
            "https://www.facebook.com/Telgorithm/",
            "https://twitter.com/telgorithm",
            "https://www.linkedin.com/company/telgorithm",
          ],
        }}
      />
    </>
  );
};

export const GatsbySeoComponent: FC<{ canonicalUrl: string }> = ({
  canonicalUrl,
}) => {
  return <GatsbySeo canonical={canonicalUrl} />;
};

export const MetaData: FC<{ title: string; description: string }> = ({
  title,
  description,
}) => {
  return (
    <Helmet title={title}>
      <meta property="og:title" content={title} />
      <meta property="twitter:title" content={title} />
      <meta name="description" content={description} />
      <meta property="og:description" content={description} />
      <meta property="twitter:description" content={description} />
    </Helmet>
  );
};
